<template>
   <div id="container">
       <h1>LOGIN Formular </h1>
       <Form class="formwrapper" @submit="processLogin" >
            <font-awesome-icon icon="user-secret" class="fa-1x" color="red" style="margin-right:5px" />
            <div class="form-element">
            <Field 
                type="email"
                name="email"
                placeholder="Email"
                v-model="email"
                :rules="validateEmail"
                />
            <ErrorMessage name="email" />
            </div>
            <br>
            <div class="form-element">
            <Field
                type="password"
                name="password"
                placeholder="Passwort"
                v-model="password"
                :rules="validatePassword"
            />
            <ErrorMessage name="password" />
            </div>
            <br>
            <a href="">Passwort vergessen?</a>
             <div class="form-element">
            <input 
                type="submit"
                value="Login"
            />
             </div>

       </form>
   </div> 
</template>

<script>
import {ref} from 'vue'
import {Field,Form,ErrorMessage } from 'vee-validate'
export default {
    props: {
        serverUrl : {
            type:String,
            required:true
        }
    },
    components: {
        Field,
        Form,
        ErrorMessage
    },
    setup(props,context) {
        let email=ref('')
        let password=ref('')
        let errorMessage=ref('')

        function processLogin(values) {
            console.log("Login::processLogin , Values: " + values)
            /*let result = {
                userExist:false,
                isAllowed:false
            }
            */
            fetch(`${props.serverUrl}?user=${email.value}&password=${password.value}`)
                .then(response => response.json())
                .then(data =>{
                    console.log("fetch ...")
                    if (!data.userExist)    {
                        console.log(`User ${email.value} does not exist.`)
                        errorMessage.value=`User ${email.value} does not exist.`
                    } else if (data.isAllowed) {
                        console.log(`User ${email.value} is allowed.`)
                        context.emit("loginDone",email.value,data.class,data.isAdmin)
                    }else {
                       console.log('Password false') 
                       errorMessage.value=`Password is wrong.`
                    }


                })


            
        }

        function validateEmail(value) {
            // if the field is empty
            if (!value) {
                return 'This field is required';
            }
            // if the field is not a valid email
            const regex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i;
            if (!regex.test(value)) {
                return 'This field must be a valid email';
            }
            return true;
        }
        function validatePassword(value) {
            if (!value)  return 'This field is required';
            
            if (value.length < 4) return 'Length of password is too short!'
            return true
        }

        return {
            email,
            password,
            processLogin,
            validateEmail,
            validatePassword,
            errorMessage
        }
    }
}
</script>

<style scoped>
#container {
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
}
.formwrapper  {
    display:flex;
    flex-direction: column;
    align-items: center;
    background: lightgoldenrodyellow;
    margin-top:40px;
    padding: 50px 150px;
}

.form-element input {
    height:45px;
    width:320px;
    border-radius:2%;
    font-size:20px;
    padding-left: 5%; 
    margin: 2%;
}

.form-element .btn {
    width: 150px;
    border:none;
    background: blue;
    color:#eee;
    font-weight: bold;
    cursor:pointer;
    font-size:1em;
    transition: all ease 0.5s;
}
</style>