<template>
  <div> 
    <Header :name="stundenplanParams.name"
            :errorMessage="errorMessage" />

    
    <Stundenplan 
      v-if="loginState" 
      :stundenplanParams="stundenplanParams" 
      @removeLesson="lessonRemove"
      @moveLessonDown="lessonMoveDown"
      @moveLessonUp="lessonMoveUp"
      @addUnit="addUnit"
      @deleteTimetable="deleteTimetable"
      />
    <Login v-else 
      @loginDone="loginDone"
      serverUrl="http://localhost:3000/passwd"
    />  
  </div>
</template>

<script>
import Stundenplan from './components/Stundenplan.vue'
import Header from './components/Header.vue'
import Login from "./components/Login.vue"
import {ref} from 'vue'

const URL_PATH="http://localhost:3000/"

export default {
  name: "App",
  components: {
    Stundenplan,
    Header,
    Login
  },
  setup() {
    let errorMessage = ref('')
    let loginState=ref(false)
    let stundenplanParams = ref({
      name: '',
      klasse: "",
      datum: "11.10.2021",
      isAdmin:false,
      stundenplan :[
        /* {begin:'7:50', end:'8:40', title:'SEW'},
        {begin:'8:45', end:'9:35', title:'REL'},
        {begin:'9:40', end:'10:30', title:'ITP'},
        {begin:'10:45', end:'11:35', title:'Deutsch'},
        {begin:'11:40', end:'12:30', title:'Englisch'}, */
      ]
    })


    function getTimetable(className,username,isAdmin) {
      let url = `${URL_PATH}timetable?class=${className}`

      fetch(url)
        .then(response => response.json())
        .then(data =>{
             console.log("fetch ...")
              if (data.tableExist)    {
                  stundenplanParams.value.stundenplan=data.timetable
                  stundenplanParams.value.klasse= className
                  stundenplanParams.value.name=username
                  stundenplanParams.value.isAdmin=isAdmin
              } else {
                  console.log('Password false') 
              }

        })
    }

    function loginDone(userName,className,isAdmin) {
      console.log("App::loginDone()")
      getTimetable(className,userName,isAdmin)
      loginState.value = true
    }
    function lessonRemove(index) {
      console.log("Remove item with index: " + index)
      stundenplanParams.value.stundenplan.splice(index,1)
    }

    function lessonMoveUp(index) {
      console.log("ParentComponent - move up "+index)
      if (index == 0) return

      let tmp=stundenplanParams.value.stundenplan[index]
      stundenplanParams.value.stundenplan[index]=stundenplanParams.value.stundenplan[index-1]
      stundenplanParams.value.stundenplan[index-1]=tmp

    }

    function lessonMoveDown(index) {
      console.log("ParentComponent - move down " + index)
      if (index == stundenplanParams.value.stundenplan.length-1) return
      let tmp=stundenplanParams.value.stundenplan[index]
      stundenplanParams.value.stundenplan[index]=stundenplanParams.value.stundenplan[index+1]
      stundenplanParams.value.stundenplan[index+1]=tmp
    }


  function deleteTimeTable(classN) {
      console.log("deleteTimeTable (App)")
      
      const requestOption = {
        method:'DELETE',
        headers: {'Content-Type':'application/json'},
        body:JSON.stringfy({
          className:classN
        })
      }
      let url = `${URL_PATH}timetable`
        
      fetch(url,requestOption)
        .then(response => response.json())
        .then(data =>{
             console.log("fetch ...")
              if (data.done==true)    {
                  console.log("Timetable has been removed ....")
              } else {
                  console.log('Password false') 
              }
        })
  }


  
  // 00:00
    function isTime(field,fieldName) {
      let ti=field.split(':')
      errorMessage.value=''
      let number = parseInt(ti[0],10)
      if (isNaN(number))
        errorMessage.value="Dies ist kein g체ltiger Wert (Feld: " + fieldName + ")"
      else {
        if (number < 7 || number >17)
           errorMessage.value="Der eingegebene Zahlenwert f채llt aus dem Zahlenwert (Feld: " + fieldName + ")"
      }
      return errorMessage.value.length == 0
    }
    

    function checkForEmtpyFields(newE) {
      errorMessage.value=''
      if (newE.begin == '' ) 
        errorMessage.value="Das Feld mit der Beginn-Zeit darf nicht leer sein!"
      else if (newE.end == '' ) 
        errorMessage.value="Das Feld mit der Ende-Zeit darf nicht leer sein!"
      else if (newE.title == '' ) 
        errorMessage.value="Das Feld mit dem Fach darf nicht leer sein!"
      else {
        if (!isTime(newE.begin,"Beginn"))
          return false;
         if (!isTime(newE.end,"Ende"))
          return false;
        
        return true
      }
      return false
    }


    function addUnit(newE) {
      console.log("Parent addUnit: " + newE)
      if (!checkForEmtpyFields(newE))
        return;
      let newEntry = {
        begin: newE.begin,
        end: newE.end,
        title: newE.title
      }
      stundenplanParams.value.stundenplan.push(newEntry)
      newE.begin=''
      newE.end=''
      newE.title=''
    }

    return {
      stundenplanParams,
      lessonRemove,
      lessonMoveUp,
      lessonMoveDown,
      addUnit,
      errorMessage,
      loginState,
      loginDone,
      deleteTimeTable


    }

  }

  
}
</script>

