<template>
    <div>
    <!-- <div id="header">
        <h1>Stundenplan f체r:  </h1>
        <h3>{{stundenplanParams.name}}</h3>
    </div> -->

    <div id='block'>
        <h2>Klasse: {{stundenplanParams.klasse}}</h2>
        

        <h2>Datum:  {{ stundenplanParams.datum }}</h2>
       
        <table>
            <thead>
                <tr>
                    <th>
                        Beginn
                    </th>
                    <th>
                        Ende
                    </th>
                    <th>
                        Fach
                    </th>
                </tr>
            </thead>

            <tbody>
                <tr v-for="(item,index) in stundenplanParams.stundenplan" :key="index"
                >
                <td>{{item.begin}}</td>
                <td>{{item.end}}</td>
                <td>{{item.title}}</td>
                <td v-if="stundenplanParams.isAdmin==true">
                    <button @click="onUpLesson(index)">UP</button>
                    <button @click="onDownLesson(index)">DOWN</button>
                    <button @click="removeLesson(index)">DEL</button>
                
                </td>
                </tr>
               
            </tbody>
            <tfoot v-if="stundenplanParams.isAdmin==true">
                <tr>
                    <td>
                        <input 
                            type='text'
                            v-model="newEntry.begin" 
                            placeholder="Beginn (erforderlich) "/>

                    </td>
                    <td>
                        <input 
                            type='text'
                            v-model="newEntry.end" 
                            placeholder="Ende (erforderlich) "/>

                    </td>
                    <td>
                        <input 
                            type='text'
                            v-model="newEntry.title" 
                            placeholder="Fach "/>

                    </td>
                    <td>
                        <button @click="unitAdd">ADD</button>

                    </td>
                </tr>
            </tfoot>
        </table>
        <div v-if="stundenplanParams.isAdmin==true" id="adminContainer">
            <button v-on:click="updateTimetable">
                 <font-awesome-icon icon="edit" class="fa-2x" color="grey" style="margin-right:3px" />
                Update
            </button>
            <button v-on:click="deleteTimetable">
                 <font-awesome-icon icon="trash" class="fa-2x" color="grey" style="margin-right:3px" />
                Delete
            </button>


        </div>

        <!-- <ul id="listRendering">
            <li v-for="(item,index) in stundenplanParams.stundenplan" :key="index">
                {{ item.begin }}
                {{ item.end }}
                {{ item.title }}
            </li>
        </ul> -->
    </div>
        
    </div>

   

</template>


<script>

//import {ref} from 'vue'

export default {
    name: "Stundenplan",
    props: {
        stundenplanParams: {
            type: Object,
            required:true
        }

    },
     setup(props, context) {
         
         let newEntry = {
             begin:'',
             end:'',
             title:''
         }
        function onUpLesson(index) {
             console.log("onUpLesson, Index: " + index)
             context.emit('moveLessonUp', index)
             
        }
        function onDownLesson(index) {
             console.log("onDownLesson Index: " + index)
             context.emit('moveLessonDown', index)
             
        }
        function removeLesson(index) {
             console.log("removeLesson " + index )
             context.emit('removeLesson', index)
        }
        function unitAdd() {
            console.log("Unit add")
            context.emit("addUnit",newEntry)
        }

        function updateTimetable() {

        }
        function deleteTimetable() {
            console.log("delete Timetable (Stundenplan)"+props.stundenplanParams.klasse)
            context.emit("deleteTimetable",props.stundenplanParams.klasse)
        }
        return {
           onUpLesson,
           onDownLesson,
           removeLesson,
           newEntry,
           unitAdd,
           updateTimetable,
           deleteTimetable

        }
    } 

    
}
</script>

<style scoped>

#header {
    display: flex;
    align-items: baseline;
    justify-content: center;
}
#header h3 {
    margin-left:20px;
}
ul {
    list-style: none;
}
#block {
    text-align: left;
    margin-left: 50px;
    margin-top: 35px;
}

table {
    width:100%;
}
</style>